* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

body {
  background-color: hsl(235, 18%, 26%);
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
main {
  width: 375px;
  background-color: white;
}
.one {
  background-image: url(illustration-sign-up-mobile.svg);
  height: 285px;
  border-bottom-left-radius: 20px;
  border-bottom-right-radius: 20px;
}
.two {
  padding: 20px;
}
.intro h1 {
  font-size: xx-large;
  font-weight: 900;
  margin-bottom: 20px;
}
.intro p {
  font-size: large;
  font-weight: 500;
  margin-bottom: 20px;
}
ul {
  list-style-type: none;
  margin-bottom: 20px;
}
li {
  margin-left: 20px;
  position: relative;
  margin-bottom: 15px;
}
li::before {
  content: '';
  position: absolute;
  background-image: url(icon-list.svg);
  background-size: cover;
  width: 14px;
  height: 14px;
  left: -20px;
  top: 2px;

}
.hidden-text {
  display: none;
  
}
.show-text {
  color: tomato;
}
form label {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 10px;
}
form input {
  padding: 15px;
  width: 100%;
  border: 1px solid gray;
  border-radius: 8px;
  margin-bottom: 20px;
}
form button {
  border: none;
  width: 100%;
  padding: 15px;
  background-color: black;
  color: white;
  border-radius: 8px;
}
form button:hover {
  background-color: tomato;
}
aside {
  width: 375px;
  background-color: white;
  padding: 20px;
}
aside .success-img {
  background-image: url(icon-success.svg);
  background-repeat: no-repeat;
  width: 200px;
  height: 100px;
  margin-top: 100px;

}
aside h2 {
  margin-bottom: 30px;
}
aside p {
  margin-bottom: 20px;
}
aside button {
  margin: 200px auto 30px;
  padding: 15px;
  width: 100%;
  background-color: black;
  color: white;
  border-radius: 8px;
  border: 0;
  font-size: larger;
}
aside button:hover {
  background-color: tomato;
}

@media only screen and (min-width: 700px) {
  body {
    display: flex;
    align-items: center;
    height: 100vh;

  }
  main {
    width: 650px;
    display: grid;
    grid-template-areas: 
        "left right";
        border-radius: 20px;
  }
  .one {
    grid-area: right;
    background-image: url(illustration-sign-up-desktop.svg);
    background-repeat: no-repeat;
    width: 350px;
    height: 90%;
    background-size: contain;
    margin: 20px 0;
  }
  .two {
    grid-area: left;
    width: 380px;
  }
 
  aside {
    display: flex;
    flex-direction: column;
    border-radius: 15px;
    height: 300px;
    width: 375px;
    background-color: white;
    padding: 0px 20px;
  }
  aside .success-img {
    background-image: url(icon-success.svg);
    background-repeat: no-repeat;
    width: 70px;
    height: 70px;
    margin-top: 10px;
  }
  aside h2 {
    margin-bottom: 10px;
  }
  aside p {
    margin-bottom: 10px;
  }
  aside button {
    margin: 20px auto 10px;
    padding: 15px;
    width: 100%;
    background-color: black;
    color: white;
    border-radius: 8px;
    border: 0;
    font-size: larger;
  }
  aside button:hover {
    background-color: tomato;
  }
  
  
}